import { cssProps } from '@oracle/oraclejet-cssprops';
import {
  isSeverityIconNeeded,
  MessageStartIcon,
  MessageSummary,
  MessageSeverity,
  severityBasedStyleClass
} from '../UNSAFE_Message';
import { classNames } from '../utils/UNSAFE_classNames';

const baseStyles = cssProps`${{
  boxSizing: 'border-box',
  // TODO: Use Flex Component instead of CSS
  display: 'flex',
  flexWrap: 'wrap'
}}`;

/**
 * A type for a single component message
 */
export type ComponentMessageItem = {
  summary?: string;
  detail?: string;
  severity?: Exclude<MessageSeverity, 'none'>;
};

/**
 * Props for the Message Component
 */
type Props = {
  /**
   * The severity of the component message
   */
  severity?: ComponentMessageItem['severity'];

  /**
   * The error detail text for the component message
   */
  detail?: ComponentMessageItem['detail'];
};

/**
 * The component that renders an individual message for inline component messaging.
 */
export function ComponentMessage({ severity = 'error', detail }: Props) {
  const classes = classNames([baseStyles, severityBasedStyleClass(severity, 'inline')]);

  return (
    <div class={classes} role="alert" aria-atomic="true">
      {isSeverityIconNeeded(severity) && <MessageStartIcon variant="inline" severity={severity} />}
      {/* We will be using the MessageSummary component, as this aligns with the style required */}
      <MessageSummary variant="inline" text={detail} />
    </div>
  );
}
